* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --primary-color: #1d4ed8;
  --primary-dark: #1e3a8a;
  --success-color: #059669;
  --warning-color: #d97706;
  --danger-color: #dc2626;
  --text-primary: #111827;
  --text-secondary: #4b5563;
  --bg-light: #f3f4f6;
  --bg-white: #fff;
  --border-color: #d1d5db;
  --shadow-sm: 0 1px 2px 0 #0000000f;
  --shadow-md: 0 4px 6px -1px #0000001f;
  --shadow-lg: 0 10px 15px -3px #0000001f;
  --shadow-xl: 0 20px 25px -5px #0000001f;
}

body {
  color: var(--text-primary);
  background-color: var(--bg-light);
  font-family: Inter, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, sans-serif;
  line-height: 1.6;
}

.app {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.header {
  color: #fff;
  box-shadow: var(--shadow-md);
  background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
  padding: 2rem 0;
}

.header-content {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 1.5rem;
  display: flex;
}

.header-left {
  flex: 1;
}

.header-title {
  align-items: center;
  gap: .5rem;
  margin-bottom: .5rem;
  font-size: 2rem;
  font-weight: 700;
  display: flex;
}

.header-title .icon {
  font-size: 2.5rem;
}

.header-subtitle {
  opacity: .95;
  font-size: 1.1rem;
  font-weight: 400;
}

.header-right .stats {
  gap: 1.5rem;
  display: flex;
}

.stat-item {
  background: #ffffff26;
  border-radius: 8px;
  align-items: center;
  gap: .5rem;
  padding: .5rem 1rem;
  display: flex;
}

.stat-icon {
  font-size: 1.5rem;
}

.stat-text {
  font-size: .9rem;
  font-weight: 500;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
}

.main-content {
  flex: 1;
  padding: 2rem 0;
}

.filter-bar {
  background: var(--bg-white);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  margin-bottom: 2rem;
  padding: 1.5rem;
}

.filter-bar-top {
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

.search-container {
  flex: 1;
  min-width: 250px;
  position: relative;
}

.search-input {
  border: 2px solid var(--border-color);
  border-radius: 8px;
  width: 100%;
  padding: .75rem 2.5rem .75rem 1rem;
  font-size: 1rem;
  transition: border-color .2s;
}

.search-input:focus {
  border-color: var(--primary-color);
  outline: none;
}

.search-icon {
  font-size: 1.2rem;
  position: absolute;
  top: 50%;
  right: 1rem;
  transform: translateY(-50%);
}

.view-toggle {
  gap: .5rem;
  display: flex;
}

.view-btn {
  border: 2px solid var(--border-color);
  background: var(--bg-white);
  cursor: pointer;
  border-radius: 8px;
  padding: .75rem 1.5rem;
  font-size: .95rem;
  font-weight: 500;
  transition: all .2s;
}

.view-btn:hover {
  border-color: var(--primary-color);
  color: var(--primary-color);
}

.view-btn.active {
  background: var(--primary-color);
  color: #fff;
  border-color: var(--primary-color);
}

.filter-bar-bottom {
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  display: flex;
}

.filters {
  flex-wrap: wrap;
  gap: .75rem;
  display: flex;
}

.filter-select {
  border: 2px solid var(--border-color);
  background: var(--bg-white);
  cursor: pointer;
  border-radius: 8px;
  padding: .6rem 1rem;
  font-size: .9rem;
  transition: border-color .2s;
}

.filter-select:focus {
  border-color: var(--primary-color);
  outline: none;
}

.btn-reset {
  background: var(--danger-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  padding: .6rem 1.25rem;
  font-size: .9rem;
  font-weight: 500;
  transition: opacity .2s;
}

.btn-reset:hover {
  opacity: .9;
}

.fountain-count {
  color: var(--text-secondary);
  font-size: .95rem;
}

.fountain-count strong {
  color: var(--primary-color);
  font-size: 1.1rem;
}

.fountain-list {
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 1.5rem;
  display: grid;
}

.fountain-card {
  background: var(--bg-white);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
  cursor: pointer;
  border-radius: 12px;
  flex-direction: column;
  height: 100%;
  min-height: 300px;
  padding: 1.5rem;
  transition: all .3s;
  display: flex;
}

.fountain-card:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.fountain-card-header {
  gap: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

.fountain-type-icon {
  color: #2563eb;
  background: #eff6ff;
  border-radius: 10px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  font-size: 1.8rem;
  display: flex;
}

.fountain-info {
  flex: 1;
}

.fountain-name {
  color: var(--text-primary);
  margin-bottom: .25rem;
  font-size: 1.2rem;
  font-weight: 600;
}

.fountain-location {
  color: var(--text-secondary);
  min-height: 2.8rem;
  font-size: .9rem;
  line-height: 1.4;
}

.fountain-card-body {
  flex-direction: column;
  flex: 1;
  margin-bottom: 1rem;
  display: flex;
}

.fountain-details {
  flex-direction: column;
  gap: .5rem;
  display: flex;
}

.detail-item {
  gap: .5rem;
  font-size: .9rem;
  display: flex;
}

.detail-label {
  color: var(--text-secondary);
  font-weight: 600;
}

.detail-value {
  color: var(--text-primary);
}

.detail-value.small {
  font-size: .85rem;
}

.fountain-card-footer {
  border-top: 1px solid var(--border-color);
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  display: flex;
}

.status-badge {
  border-radius: 6px;
  align-items: center;
  padding: .4rem .8rem;
  font-size: .85rem;
  font-weight: 700;
  display: inline-flex;
}

.status-working {
  color: #064e3b;
  background: #a7f3d0;
}

.status-maintenance {
  color: #7c2d12;
  background: #fdba74;
}

.status-offline {
  color: #7f1d1d;
  background: #fca5a5;
}

.btn-details {
  color: var(--primary-color);
  cursor: pointer;
  background: none;
  border: none;
  padding: .5rem 1rem;
  font-size: .9rem;
  font-weight: 600;
  transition: color .2s;
}

.btn-details:hover {
  color: var(--primary-dark);
}

.fountain-map-placeholder {
  background: var(--bg-white);
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
  border-radius: 12px;
  padding: 2rem;
}

.map-header {
  text-align: center;
  margin-bottom: 2rem;
}

.map-header h3 {
  margin-bottom: .5rem;
  font-size: 1.8rem;
}

.map-info {
  color: var(--text-secondary);
  font-size: 1rem;
}

.coordinate-list {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.coordinate-item {
  background: var(--bg-light);
  cursor: pointer;
  border-radius: 8px;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  transition: all .2s;
  display: flex;
}

.coordinate-item:hover {
  background: #e0e7ff;
  transform: translateX(4px);
}

.coordinate-icon {
  color: #2563eb;
  background: #eff6ff;
  border-radius: 8px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 45px;
  font-size: 1.5rem;
  display: flex;
}

.coordinate-info {
  flex: 1;
}

.coordinate-info h4 {
  margin-bottom: .25rem;
}

.coordinate-location {
  color: var(--text-secondary);
  margin-bottom: .25rem;
  font-size: .9rem;
}

.coordinates {
  color: var(--text-secondary);
  font-family: Courier New, monospace;
  font-size: .85rem;
}

.btn-map-details {
  background: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: .5rem 1rem;
  font-weight: 500;
  transition: opacity .2s;
}

.btn-map-details:hover {
  opacity: .9;
}

.map-footer {
  border-top: 1px solid var(--border-color);
  margin-top: 2rem;
  padding-top: 1.5rem;
}

.map-footer .note {
  color: var(--text-secondary);
  font-size: .9rem;
  line-height: 1.6;
}

.modal-overlay {
  z-index: 2000;
  background: #00000080;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal-content {
  background: var(--bg-white);
  width: 650px;
  max-width: 95%;
  box-shadow: var(--shadow-xl);
  border-radius: 14px;
  animation: .2s ease-out fadeIn;
  overflow: hidden;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(.97);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

.modal-header {
  border-bottom: 1px solid var(--border-color);
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
  padding: 1.25rem 1.5rem;
  display: flex;
}

.modal-title-section h2 {
  color: var(--text-primary);
  align-items: center;
  gap: .5rem;
  margin: 0;
  font-size: 1.4rem;
  font-weight: 700;
  display: flex;
}

.modal-title-section i {
  color: var(--primary-color);
