.modal-close:hover {
  color: var(--text-primary);
}

.modal-body {
  padding: 1.5rem;
}

.details-grid {
  grid-template-columns: 1fr;
  row-gap: .75rem;
  margin-bottom: 1.25rem;
  display: grid;
}

.details-grid .detail-item {
  gap: .5rem;
  font-size: .95rem;
  display: flex;
}

.details-grid .detail-label {
  color: var(--text-primary);
  font-weight: 700;
}

.details-grid .detail-value {
  color: var(--text-secondary);
}

.quick-update-section {
  margin-top: 1rem;
}

.quick-update-section h3 {
  margin-bottom: .5rem;
  font-size: 1rem;
}

.quick-update-buttons {
  flex-wrap: wrap;
  gap: .5rem;
  display: flex;
}

.btn-update {
  cursor: pointer;
