  border-radius: 8px;
  padding: .5rem .9rem;
  font-size: .9rem;
  font-weight: 600;
}

.btn-update.working {
  background: var(--success-color);
}

.btn-update.needs-maintenance {
  background: var(--warning-color);
}

.btn-update.out-of-service {
  background: var(--danger-color);
}

.btn-update:disabled {
  opacity: .6;
  cursor: not-allowed;
}

.reports-section {
  margin-top: 1.5rem;
}

.reports-header {
  justify-content: space-between;
  align-items: center;
  margin-bottom: .75rem;
  display: flex;
}

.btn-add-report {
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  cursor: pointer;
  background: #fff;
  border-radius: 8px;
  padding: .45rem .8rem;
  font-size: .9rem;
  font-weight: 600;
}

.btn-add-report:hover {
  background: var(--primary-color);
  color: #fff;
}

.reports-list {
  flex-direction: column;
  gap: 1rem;
  display: flex;
}

.report-item {
  background: var(--bg-light);
  border-left: 4px solid var(--primary-color);
  border-radius: 8px;
  padding: 1rem;
}

.report-header {
  justify-content: space-between;
  margin-bottom: .5rem;
  display: flex;
}

.report-type {
  text-transform: capitalize;
  color: var(--text-primary);
  font-weight: 600;
}

.report-date {
  color: var(--text-secondary);
  font-size: .85rem;
}

.report-description {
  color: var(--text-secondary);
  margin: 0;
  font-size: .9rem;
}

.modal-footer {
  border-top: 1px solid var(--border-color);
  justify-content: flex-end;
  padding: 1rem 1.5rem;
  display: flex;
}

.report-form {
  flex-direction: column;
  gap: 1.25rem;
  display: flex;
}

.form-group {
  flex-direction: column;
  gap: .5rem;
