  font-size: .9rem;
}

.form-actions {
  padding-top: .5rem;
}

.btn-submit {
  background: var(--primary-color);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  gap: .5rem;
  width: 100%;
  padding: .875rem 1.5rem;
  font-size: 1rem;
  font-weight: 600;
  transition: opacity .2s;
  display: flex;
}

.btn-submit:disabled {
  opacity: .6;
  cursor: not-allowed;
}

.btn-submit:not(:disabled):hover {
  opacity: .9;
}

.message {
  border-radius: 8px;
  margin-bottom: 1.5rem;
  padding: 1rem 1.25rem;
  font-weight: 500;
}

.message.success {
  color: #065f46;
  background: #d1fae5;
}

.message.error {
  color: #991b1b;
