}

.loading-container {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem 2rem;
  display: flex;
}

.spinner {
  border: 4px solid var(--border-color);
  border-top-color: var(--primary-color);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  margin-bottom: 1rem;
  animation: 1s linear infinite spin;
}

.spinner-small {
  border: 2px solid #ffffff4d;
  border-top-color: #fff;
  border-radius: 50%;
  width: 16px;
