* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --primary-color: #1d4ed8;   /* darker blue */
  --primary-dark: #1e3a8a;

  --success-color: #059669;   /* darker green */
  --warning-color: #d97706;   /* darker orange */
  --danger-color: #dc2626;    /* darker red */

  --text-primary: #111827;
  --text-secondary: #4b5563;

  --bg-light: #f3f4f6;        /* slightly darker background */
  --bg-white: #ffffff;

  --border-color: #d1d5db;    /* darker border */

  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.12);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.12);
  --shadow-xl: 0 20px 25px -5px rgba(0, 0, 0, 0.12);
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  color: var(--text-primary);
  background-color: var(--bg-light);
  line-height: 1.6;
}

.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header */
.header {
  background: linear-gradient(135deg, #2563eb 0%, #1e40af 100%);
  color: white;
  padding: 2rem 0;
  box-shadow: var(--shadow-md);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1.5rem;
}

.header-left { flex: 1; }

.header-title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.header-title .icon { font-size: 2.5rem; }

.header-subtitle {
  font-size: 1.1rem;
  opacity: 0.95;
  font-weight: 400;
}

.header-right .stats {
  display: flex;
  gap: 1.5rem;
}

.stat-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.15);
  border-radius: 8px;
}

.stat-icon { font-size: 1.5rem; }
.stat-text { font-size: 0.9rem; font-weight: 500; }

/* Container */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
  width: 100%;
}

/* Main Content */
.main-content {
  flex: 1;
  padding: 2rem 0;
}

/* Filter Bar */
.filter-bar {
  background: var(--bg-white);
  border-radius: 12px;
  padding: 1.5rem;
  margin-bottom: 2rem;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
}

.filter-bar-top {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

.search-container {
  flex: 1;
  position: relative;
  min-width: 250px;
}

.search-input {
  width: 100%;
  padding: 0.75rem 2.5rem 0.75rem 1rem;
  border: 2px solid var(--border-color);
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.2s;
}

.search-input:focus {
  outline: none;
  border-color: var(--primary-color);
}

.search-icon {
  position: absolute;
  right: 1rem;
  top: 50%;
  transform: translateY(-50%);
  font-size: 1.2rem;
}

.view-toggle { display: flex; gap: 0.5rem; }

.view-btn {
  padding: 0.75rem 1.5rem;
  border: 2px solid var(--border-color);
  background: var(--bg-white);
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.95rem;
  font-weight: 500;
  transition: all 0.2s;
}

.view-btn:hover {
  border-color: var(--primary-color);
  color: var(--primary-color);
}

.view-btn.active {
  background: var(--primary-color);
  color: white;
  border-color: var(--primary-color);
}

.filter-bar-bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.filters { display: flex; gap: 0.75rem; flex-wrap: wrap; }

.filter-select {
  padding: 0.6rem 1rem;
  border: 2px solid var(--border-color);
  border-radius: 8px;
  background: var(--bg-white);
  font-size: 0.9rem;
  cursor: pointer;
  transition: border-color 0.2s;
}

.filter-select:focus {
  outline: none;
  border-color: var(--primary-color);
}

.btn-reset {
  padding: 0.6rem 1.25rem;
  background: var(--danger-color);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 500;
  transition: opacity 0.2s;
}

.btn-reset:hover { opacity: 0.9; }

.fountain-count {
  font-size: 0.95rem;
  color: var(--text-secondary);
}

.fountain-count strong {
  color: var(--primary-color);
  font-size: 1.1rem;
}

/* Fountain List */
.fountain-list {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 1.5rem;
}

/* Fountain Card */
.fountain-card {
  background: var(--bg-white);
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  flex-direction: column;
  min-height: 300px;
  height: 100%;
}

.fountain-card:hover {
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.fountain-card-header {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}

.fountain-type-icon {
  font-size: 1.8rem;
  flex-shrink: 0;
  color: #2563eb;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  background: #eff6ff;
  border-radius: 10px;
}

.fountain-info { flex: 1; }

.fountain-name {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.25rem;
  color: var(--text-primary);
}

.fountain-location {
  font-size: 0.9rem;
  color: var(--text-secondary);
  line-height: 1.4;
  min-height: 2.8rem;
}

.fountain-card-body {
  margin-bottom: 1rem;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.fountain-details {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.detail-item {
  display: flex;
  gap: 0.5rem;
  font-size: 0.9rem;
}

.detail-label { font-weight: 600; color: var(--text-secondary); }
.detail-value { color: var(--text-primary); }
.detail-value.small { font-size: 0.85rem; }

.fountain-card-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  border-top: 1px solid var(--border-color);
}

.status-badge {
  display: inline-flex;
  align-items: center;
  padding: 0.4rem 0.8rem;
  border-radius: 6px;
  font-size: 0.85rem;
  font-weight: 700;
}

/* Darker status colors */
.status-working {
  background: #a7f3d0;
  color: #064e3b;
}

.status-maintenance {
  background: #fdba74;
  color: #7c2d12;
}

.status-offline {
  background: #fca5a5;
  color: #7f1d1d;
}

.btn-details {
  padding: 0.5rem 1rem;
  background: transparent;
  color: var(--primary-color);
  border: none;
  cursor: pointer;
  font-weight: 600;
  font-size: 0.9rem;
  transition: color 0.2s;
}

.btn-details:hover { color: var(--primary-dark); }

/* Map Placeholder */
.fountain-map-placeholder {
  background: var(--bg-white);
  border-radius: 12px;
  padding: 2rem;
  box-shadow: var(--shadow-sm);
  border: 1px solid var(--border-color);
}

.map-header { text-align: center; margin-bottom: 2rem; }

.map-header h3 {
  font-size: 1.8rem;
  margin-bottom: 0.5rem;
}

.map-info { color: var(--text-secondary); font-size: 1rem; }

.coordinate-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.coordinate-item {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 1rem;
  background: var(--bg-light);
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
}

.coordinate-item:hover {
  background: #e0e7ff;
  transform: translateX(4px);
}

.coordinate-icon {
  font-size: 1.5rem;
  flex-shrink: 0;
  color: #2563eb;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 45px;
  height: 45px;
  background: #eff6ff;
  border-radius: 8px;
}

.coordinate-info { flex: 1; }

.coordinate-info h4 { margin-bottom: 0.25rem; }

.coordinate-location {
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin-bottom: 0.25rem;
}

.coordinates {
  font-size: 0.85rem;
  color: var(--text-secondary);
  font-family: 'Courier New', monospace;
}

.btn-map-details {
  padding: 0.5rem 1rem;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: 500;
  transition: opacity 0.2s;
}

.btn-map-details:hover { opacity: 0.9; }

.map-footer {
  margin-top: 2rem;
  padding-top: 1.5rem;
  border-top: 1px solid var(--border-color);
}

.map-footer .note {
  color: var(--text-secondary);
  font-size: 0.9rem;
  line-height: 1.6;
}

/* ---------------------------- */
/* MODAL OVERLAY & CONTAINER    */
/* ---------------------------- */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
  padding: 1rem;
}

.modal-content {
  background: var(--bg-white);
  width: 650px;
  max-width: 95%;
  border-radius: 14px;
  box-shadow: var(--shadow-xl);
  overflow: hidden;
  animation: fadeIn 0.2s ease-out;
}

@keyframes fadeIn {
  from { opacity: 0; transform: scale(0.97); }
  to { opacity: 1; transform: scale(1); }
}

/* ---------------------------- */
/* MODAL HEADER                 */
/* ---------------------------- */
.modal-header {
  padding: 1.25rem 1.5rem;
  border-bottom: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
}

.modal-title-section h2 {
  margin: 0;
  font-size: 1.4rem;
  font-weight: 700;
  color: var(--text-primary);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.modal-title-section i {
  color: var(--primary-color);
  font-size: 1.3rem;
}

.modal-location {
  margin-top: 0.25rem;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.modal-close {
  background: transparent;
  border: none;
  font-size: 1.6rem;
  cursor: pointer;
  color: var(--text-secondary);
  padding: 0;
  line-height: 1;
}

.modal-close:hover { color: var(--text-primary); }

/* ---------------------------- */
/* MODAL BODY                   */
/* ---------------------------- */
.modal-body { padding: 1.5rem; }

.details-grid {
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 0.75rem;
  margin-bottom: 1.25rem;
}

.details-grid .detail-item {
  display: flex;
  gap: 0.5rem;
  font-size: 0.95rem;
}

.details-grid .detail-label {
  font-weight: 700;
  color: var(--text-primary);
}

.details-grid .detail-value { color: var(--text-secondary); }

/* Status update */
.quick-update-section { margin-top: 1rem; }

.quick-update-section h3 {
  font-size: 1rem;
  margin-bottom: 0.5rem;
}

.quick-update-buttons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.btn-update {
  padding: 0.5rem 0.9rem;
  border-radius: 8px;
  font-size: 0.9rem;
  cursor: pointer;
  border: none;
  color: white;
  font-weight: 600;
}

.btn-update.working { background: var(--success-color); }
.btn-update.needs-maintenance { background: var(--warning-color); }
.btn-update.out-of-service { background: var(--danger-color); }

.btn-update:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Reports section */
.reports-section { margin-top: 1.5rem; }

.reports-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.75rem;
}

.btn-add-report {
  padding: 0.45rem 0.8rem;
  border-radius: 8px;
  border: 1px solid var(--primary-color);
  color: var(--primary-color);
  background: white;
  cursor: pointer;
  font-size: 0.9rem;
  font-weight: 600;
}

.btn-add-report:hover {
  background: var(--primary-color);
  color: white;
}

/* Reports list (if you render it) */
.reports-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.report-item {
  padding: 1rem;
  background: var(--bg-light);
  border-radius: 8px;
  border-left: 4px solid var(--primary-color);
}

.report-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.report-type {
  font-weight: 600;
  text-transform: capitalize;
  color: var(--text-primary);
}

.report-date {
  font-size: 0.85rem;
  color: var(--text-secondary);
}

.report-description {
  font-size: 0.9rem;
  color: var(--text-secondary);
  margin: 0;
}

/* Modal footer */
.modal-footer {
  padding: 1rem 1.5rem;
  border-top: 1px solid var(--border-color);
  display: flex;
  justify-content: flex-end;
}

/* Form Styles */
.report-form {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  font-weight: 600;
  font-size: 0.95rem;
  color: var(--text-primary);
}

.required { color: var(--danger-color); }

.form-select,
.form-input,
.form-textarea {
  padding: 0.75rem 1rem;
  border: 2px solid var(--border-color);
  border-radius: 8px;
  font-size: 1rem;
  font-family: inherit;
  transition: border-color 0.2s;
}

.form-select:focus,
.form-input:focus,
.form-textarea:focus {
  outline: none;
  border-color: var(--primary-color);
}

.form-select.error,
.form-input.error,
.form-textarea.error {
  border-color: var(--danger-color);
}

.form-textarea {
  resize: vertical;
  min-height: 100px;
}

.textarea-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.85rem;
}

.char-count { color: var(--text-secondary); }
.char-count.warning { color: var(--warning-color); font-weight: 600; }

.input-help { font-size: 0.85rem; color: var(--text-secondary); }

.error-message {
  font-size: 0.85rem;
  color: var(--danger-color);
  font-weight: 500;
}

.form-error {
  padding: 0.75rem 1rem;
  background: #fee2e2;
  color: #991b1b;
  border-radius: 8px;
  font-size: 0.9rem;
}

.form-actions { padding-top: 0.5rem; }

.btn-submit {
  width: 100%;
  padding: 0.875rem 1.5rem;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
  font-size: 1rem;
  transition: opacity 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

.btn-submit:disabled { opacity: 0.6; cursor: not-allowed; }
.btn-submit:not(:disabled):hover { opacity: 0.9; }

/* Messages */
.message {
  padding: 1rem 1.25rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  font-weight: 500;
}

.message.success {
  background: #d1fae5;
  color: #065f46;
}

.message.error {
  background: #fee2e2;
  color: #991b1b;
}

/* Loading and Error States */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 4rem 2rem;
  text-align: center;
}

.spinner {
  width: 50px;
  height: 50px;
  border: 4px solid var(--border-color);
  border-top-color: var(--primary-color);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

.spinner-small {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top-color: white;
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
  display: inline-block;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.error-container {
  text-align: center;
  padding: 4rem 2rem;
}

.error-message {
  font-size: 1.1rem;
  color: var(--danger-color);
  margin-bottom: 1.5rem;
}

.btn-retry {
  padding: 0.75rem 1.5rem;
  background: var(--primary-color);
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 500;
  font-size: 1rem;
  transition: opacity 0.2s;
}

.btn-retry:hover { opacity: 0.9; }

/* Empty State */
.empty-state {
  text-align: center;
  padding: 4rem 2rem;
}

.empty-icon {
  font-size: 3rem;
  margin-bottom: 1rem;
  color: #9ca3af;
}

.empty-state h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
}

.empty-state p {
  color: var(--text-secondary);
  font-size: 1rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .header-title { font-size: 1.5rem; }
  .header-title .icon { font-size: 1.8rem; }
  .header-subtitle { font-size: 1rem; }

  .stats {
    width: 100%;
    justify-content: center;
  }

  .filter-bar { padding: 1rem; }

  .fountain-list {
    grid-template-columns: 1fr;
  }

  .modal-content {
    width: 100%;
    max-height: 92vh;
  }

  .modal-header,
  .modal-body,
  .modal-footer {
    padding: 1rem;
  }

  .quick-update-buttons {
    flex-direction: column;
  }
}
